import * as React from "react";
import { useSnapCarousel } from 'react-snap-carousel';
import {
  IconButton,
} from '@chakra-ui/react';
import { ChevronLeftIcon, ChevronRightIcon } from '@chakra-ui/icons';
import "./styles.scss";

const Chevron = (props) => {
  const { isLeft, onClick } = props;
  const cn = isLeft ? "ScrollButton Left" : "ScrollButton Right"; 
  const Icon = isLeft ? ChevronLeftIcon : ChevronRightIcon;

  return (
    <IconButton 
      className={cn}
      variant="unstyled"
      icon={
        <Icon 
          boxSize="48px" 
          color="rgb(255, 182, 190)"
          _hover={{ color: "rgb(255, 219, 223)" }}
        />
      } 
      onClick={onClick}
  />
  );
}

const Carousel = (props) => {
  const {
    activePageIndex,
    scrollRef,
    goTo,
    snapPointIndexes,
    next,
    prev,
    pages,
  } = useSnapCarousel();

  const isAtEnd = activePageIndex === pages.length - 1;
  const isAtBeginning = activePageIndex === 0;

  const onClick = (isLeft) => {
    return () => {
      if (isLeft) {
        if (isAtBeginning) {
          goTo(pages.length - 1);
        } else {
          prev();
        }
      } else {
        if (isAtEnd) {
          goTo(0);
        } else {
          next();
        }
      }
    }
  };

  return (
    <div className="Carousel">
      <ul
        className="CarouselContainer"
        ref={scrollRef}
        style={{
          display: 'flex',
          overflow: 'auto',
          scrollSnapType: 'x mandatory'
        }}
      >
        {props.children.map((childImg, idx) => {
          return (
            <li className="CarouselImage"
              style={{
                scrollSnapAlign: snapPointIndexes.has(idx) ? "start" : ""
              }} key={idx}>
              {childImg}
            </li>
          );
        })}
      </ul>

      <Chevron 
          isLeft={true} 
          onClick={onClick(true)}
        />
      <Chevron
        isLeft={false}
        onClick={onClick(false)}
      />
    </div>
  );
};

export default Carousel;